.purchase-orders {
	.page-title{
		min-height: 32px;
		position: relative;
	}
	.comment-multi-icon {
		display: inline-block;
		vertical-align: middle;
		height: 24px;
		width: 24px;
		margin-right: 18px;
		cursor: pointer;

		svg {
			height: 24px;
			width: 24px;
		}
	}
	.page-nav-wrap {
		padding-bottom: 16px;
	}
	.export-link-btn {
		float: right;
		padding-right: 16px;
		line-height: 30px;
		font-weight: $text-weight-semi-bold;
		cursor: pointer;
	}
	&.white-bg{
		background: $white;
	}
	.empty-bag{
		display: flex;
		flex-direction: column;
		margin-top: 110px;
		align-items: center;
		text-align: center;
		.po-new-txt{
			padding: 5px;
		}
		img{
			height: 300px;
			margin-bottom: 50px;
		}
		.create-new-button{
			display: flex;
			align-items: center;
			justify-content: center;
			margin-top: 24px;
			svg {
				margin-right: 6px;
			}
		}
	}

	//Make the column separator visible in first column. This is required if first column is not checkbox
	table thead.lt-head th.lt-column.po-number-header {
		border-right: 1px solid $app-border-primary;
		padding-right: 16px !important;
	}

	//Required for space after - on list page header
	.breadcrumb-text {
		padding-left: 2px;
	}

	.po-number{
		padding-right: 16px !important;
	}
	.date-elapsed {
		fill: $app-red;
		width: 16px;
		height: 16px;
	}

	.text-semi-bold{
		font-weight: $text-weight-semi-bold;
	}

	.date-column{
		position: relative;
	}

	.date-elapsed-icon{
		position: absolute;
		top: 1px;
		right: 65px;
		padding-left: 6px;
	}
	.app-filter {
		margin-left: 16px;
		padding: 8px;
		svg {
			height: 16px;
			width: 16px;
		}
		&.active {
			background: $btnPrimaryBackground;
			border: none;
			svg{
				fill: $white;
			}
		}
	}
	.ui-list-filter{
		.filter-row ul{
			// width:100%;
		}
	}
}

.purchase-orders-table {
	th, td {
		box-sizing: content-box;
	}
}
