.po-form-section {
	background: $app-white;
	border: 0;
	padding: 16px;
	input[type=text]{
		width: 100%;
	}

	.ember-form{
		width: 775px;
	}

	.hide{
		display: none !important;
	}

	.textarea-field textarea{
		height: 134px;
	}

	.label-text {
		height: 20px;
		color: $app-form-label-color;
		font-size: $text-normal;
		font-weight: 600;
		line-height: 20px;
		padding-bottom: 10px;
		.star{
			color: $app-red;
		}
	}

	.date-text-field{
		background-color: $white;
	}

	.ember-basic-dropdown:not(.date-control){
		width: 300px;
	}
	.ember-power-calendar .ember-basic-dropdown{
		width: auto;
	}

	.vendor-select.form-field {
		margin-bottom: 8px;
	}
	.country.form-field {
		margin: 0px;
	}

	.section-header {
		font-size: 16px;
		line-height: 20px;
		font-weight: 500;
		margin-bottom: 16px;
	}
	.ember-basic-dropdown-trigger[aria-disabled=true] {
		background-color: $formActionsBackground;
		cursor: not-allowed!important;
	}
	.billing-address-trigger[aria-disabled=true] {
		background-color: $formActionsBackground;
		cursor: not-allowed!important;
	}

	.checkbox-field{
		.form-field--label{
			display: inline;
		}

		.form-field--control{
			padding: 0;
			box-shadow: none;
		}

	}

	.form-field.form-field--has-errors .address-field .ember-basic-dropdown-trigger{
		border-color: $primary-border;
	}
	.address-field {
		margin: 16px 0;
		.flex-container{
			display: flex;
			.ember-basic-dropdown{
				width: inherit;
			}
		}
		.flex-column{
			flex: 1;
		}
		.flex-column:first-child{
			margin-right: 16px;
		}
		.flex-column:last-child{
			margin-left: 16px;
		}
		.address-trigger{
			color: $linkColor;
			padding: 1px 1px 1px 12px;
			font-size: 12px;
		}
		.same-address{
			float: right;
			margin-top: -30px;
		}

		.address-location-trigger{
			border-bottom-left-radius: 0 !important;
			border-bottom-right-radius: 0 !important;
			min-height: 28px;
			margin-bottom: -49px;
			border-top: 0;
			border-right: 0;
			border-left: 0;
			height: 30px;
			overflow-y: hidden;
			width: calc(100% - 2px);
			margin-left: 1px;
			margin-top: 1px;
		}

		.address-location-trigger:hover{
			border-bottom: 1px solid $primary-border;
		}
		.ember-basic-dropdown .ember-power-select-trigger.ember-basic-dropdown-trigger--below, .ember-basic-dropdown .ember-power-select-trigger:focus{
			border-top: 0 !important;
			border-right: 0 !important;
			border-left: 0 !important;
			border-bottom: 1px solid $primary-border !important;
			box-shadow: none !important;
		}

		.details-textarea{
			padding-top: 40px;
			padding-left: 12px;
			padding-right: 12px;
			width: 380px;
		}

		.address-location-dropdown{
			margin-top: 52px;
		}

		.location {
			.ember-power-select-option {
				.level-0 {
					padding-left: 0;
				}

				.level-1 {
					padding-left: 15px;
				}

				.level-2 {
					padding-left: 30px;
				}

				.level-3 {
					padding-left: 45px;
				}

				.level-4 {
					padding-left: 60px;
				}
			}
		}
	}

	.vendor-field {
		.textarea-field {
			margin: 0;
			width: 48%;
		}
	}

	.conversion-rate{
		font-size: 12px;
	}
	.conversion-rate-input{
		display: inline-block;
		margin-top: 0 !important;
		margin-bottom: 8px !important;
		width: 72px;
		.form-field--errors{
			position: absolute;
		}
	}
}

.po-receive-table .lt-body-wrap {
	overflow-y: visible;
}

.receive-all-confirm-modal {
	background-color: $transparent-overlay-bg!important;
	width: 730px!important;
	.ember-modal-footer {
		width: 730px!important;
	}
	.ember-modal-body {
		position: relative;
	}

	.custom-modal-confirmation {
		box-sizing: border-box;
		position: absolute;
		width: 100%;
		left: 0;
		bottom: 0;
		background: $app-light-bg;
		padding: 32px;
		box-shadow: 0px 2px 18px $box-shadow-color-1;

		.title {
			padding-bottom: 12px;
		}
	}
}

.po-receive-modal-wrap {
	.form-valid{
		.error {
			border: 1px solid $app-border-primary!important;
		}
	}
	width: 730px!important;
	.ember-modal-footer {
		width: 730px!important;
	}

	.ember-alert-warning {
		height: auto !important;
		margin-bottom: 16px;
		margin-top: 16px;
		background-color: $alertWarningBackground;
		border-color: $alertWarningBorder;

		p {
			padding: 8px;
		}

		p::before {
			border-left-color: $alertWarningBorder;
		}
	}
}

.po-receive-table {
	th, td {
		box-sizing: content-box;
	}
	.lt-body td:nth-child(2) {
		.inline-wrapper {
			height: 30px;

			input.disbaled {
				background-color: $inputDisabledBackground !important;
				color: $disabled-text-color !important;

			}

			.error-message {
				width: 200px !important;
				top: 30px;
				font-size: 12px;
				font-weight: 500;
				line-height: 14px;
			}
		}

		.inline-wrapper input {
			border: 1px solid $app-footer-border-top;
			border-radius: 4px;
			padding: 4px 7px !important;
		}

	}

	.ember-light-table {
		overflow: visible !important;
		td:nth-child(1), th:nth-child(1) {
			width: 174px  !important;
		}
	}

	.error-message {
		width: 250px !important;
	}
}

.purchase-details-section{
	margin-top: 16px;
	background-color: #fff;
	padding: 16px;

	h4 {
		font-size: 16px;
		font-weight: $text-weight-semi-bold;
		margin-bottom: 16px;
	}

	.disable-pd-delete-btn path{
		fill: $btnDisabledColor
	}


	.disabled-table {
		th.lt-column{
			&:first-child{
				border-right: 1px solid $primary-border!important;
			}
		}

		.lt-body{
			td{
				&:last-child{
					border-right: hidden!important;
				}
			}
		}
	}
	.table-wrap .rule-content.ember-light-table .lt-body-wrap {
		overflow-y: visible;
		tbody.lt-body tr.lt-row {
			td.lt-cell:first-child {
				padding-left: 4px;
			}

			td.lt-cell {
				padding: 0 8px;
				padding: 0;
				&:last-child{
					border-right: none;
				}
			}
		}
		.lt-head thead tr th.lt-column:first-child{
			padding-left: 4px;
		}
	}

	.table-wrap .ember-light-table .lt-body-wrap tbody.lt-body tr.lt-row:hover{
		background-color: $white !important;
	}

	.table-wrap .rule-content.ember-light-table .lt-body-wrap tbody.lt-body tr.lt-row

	.total-item-cost{
		padding: 0 16px !important;
	}

	.rule-cell {
		height: 100%;
		padding: 16px 0;
	}

	.rule-row {
		padding: 8px 8px 0 8px;
		box-sizing: border-box;
		border-top: 0;

		&:last-child {
			border-bottom-left-radius: 4px;
			border-bottom-right-radius: 4px;
		}

		button {
			background: 0 0;
			border: none;
			outline: 0;
			padding: 0;
			margin-right: 8px;
			margin-left: 4px;
			height: 24px;

			svg {
				width: 16px;
				margin-top: 3px;
			}

			span {
				margin-left: 4px;
				font-size: $baseFontSize;
				float: right;
				line-height: 1.5;
			}
		}
		.btn-add-condition {
			margin-left: 0;
			margin-top: 8px
		}
	}

	.lt-cell {
		button {
			background: 0 0;
			border: none;
			outline: 0;
			padding: 0;
			margin-right: 8px;
			margin-left: 4px;

			svg {
				width: 16px;
			}
		}
	}

	.table-wrap .ember-light-table .lt-body-wrap tbody.lt-body tr.lt-row td.lt-cell {
		border-right: 1px solid $app-border-primary;
	}
}


.inline-wrapper {
	position: relative;
	width: 100%;
	height: 100%;

	input {
		width: 100%;
		height: 100% !important;
		border-color: transparent;
		box-shadow: none;
		padding: 11px 18px;

		&:focus, &:hover {
			border-color: transparent;
			box-shadow: none;
		}

		&.error {
			border-color: $app-red;
			box-shadow: none;
			&:focus {
				border-color: $app-red;
			}
		}
	}
	.error-message {
		position: absolute;
		top: 46px;
		left: 0;
		z-index: 9999999;
		width: 95.5%;
		border: 1px solid $app-red;
		padding: 5px;
		background-color: #FFF0F0;
		color: $app-red;
		font-weight: $text-weight-semi-bold;
		box-shadow: 0 8px 10px 0 rgba(0,0,0,0.18);
	}
}

.change-currency-modal, .clear-vendor-modal, .delete-po-modal, .close-po-modal{
	.ember-modal-footer{
		width: auto !important;
	}
}

.pd-wrap{
	margin-top: -34px;
	.pd-items{
		display: flex;
		flex-direction: row;
		margin-bottom: 8px;
		.currency-symbol{
			padding-left: 4px;
			color: $muted-secondary;
			font-weight: bold;
		}
		&--has-errors{
			margin-bottom: 0;
		}
	}
	.pd-label{
		margin-left: auto;
		margin-right: 40px;
		width: auto;
		padding: 8px;
	}
	.pd-content{
		width:152px;
		&.text-value{
			padding-bottom: 8px;
			padding-top: 8px;
		}
		&--has-errors{
			border-color: $app-red;
		}
	}

	.pd-field-error{
		text-align: right;
		color: $app-red;
		font-size: $text-normal;
		font-weight: $text-weight-medium;
	}
	.shipping-field-error {
		padding-right: 20px;
	}

	.pd-total-section{
		border-bottom: 1px $muted-secondary dashed;
		border-top: 1px $muted-secondary dashed;
		.pd-items{
			margin-bottom: 0;
		}
		.helpdesk-cost{
			font-size: 12px;
		}
	}
	&.po-ordered-state{
		margin-top: 8px;
	}
}

.date-field .ember-basic-dropdown {
	.ember-basic-dropdown-content {
		width: 270px;
	}
}

.ember-basic-dropdown.input-with-select {
	.ember-basic-dropdown-content {
		left: 0;
		height: auto;
		max-height: 245px;
		overflow-y: auto;
	}
}

.purchase-item-name{
	height: 100%;
	width: 100%;
	.input-with-select{
		height: inherit;
		width: inherit;
		.ember-basic-dropdown-trigger{
			height: inherit;
			width: inherit;
			input{
				height: inherit;
				width: inherit;
			}
		}
		.group-header{
			padding: 8px;
			span{
				font-weight: $text-weight-semi-bold;
			}
			cursor: pointer;
			svg{
				height: 20px;
				width: 20px;
				float: right;
				background: $dropdown-hover;
				border-radius: 4px;
			}
		}
		.options-list-wrap{
			margin-left: 5px;
		}
		.empty-group{
			padding-left: 10px;
			font-weight: $text-weight-semi-bold;
			color: $app-muted-color;
		}
	}
}
.margin-bottom-35 {
	margin-bottom: 35px;
}
