.po-attachments{
	.no-attachment-wrap{

		.po-attachments-desc{
			font-weight: 600;
			color: $app-form-label-color;
			text-align: center;
		}
		.no-attachments-found{
			background: url(/a/assets/images/empty-states/no-attachments.svg) 50% 75% no-repeat;
			padding-top: 0;
			background-position: center;
			height: 200px;
			width: 40%;
			margin: 100px auto 25px;

			.attach-files{
				text-align: center;
			}

		}
		.attachment-title{
			font-weight: $text-weight-normal !important;
			display: flex;
			justify-content: center;
			width: 200px;
			box-sizing: border-box;
			height: 32px;
			padding: 4px 8px;
			font-size: 14px;
			line-height: 22px;
			text-align: center;
			vertical-align: middle;
			cursor: pointer;
			background: linear-gradient(180deg, #ffffff 0%, #f5f7f9 100%);
			border: 1px solid $app-border-primary;
			border-radius: 4px;
			color: $btnPrimaryBorder !important;
			-moz-box-shadow: 0 1px 0 0 rgba(24, 50, 71, 0.05);
			-webkit-box-shadow: 0 1px 0 0 rgba(24, 50, 71, 0.05);
			box-shadow: 0 1px 0 0 rgba(24, 50, 71, 0.05);
			margin: 0 auto;
			svg{
				height: 20px;
				width: 16px;
				fill: $app-icon-color;
			}
		}
		.btn{
			margin-left: 40%;
			display: flex;
			align-items: center;
			.po-attachment-icon{
				height: 16px;
				width: 16px;
				margin-right: 5px;
			}
		}
		.po-attachment-limit{
			text-align: center;
			margin-top: 16px;
			color: $app-desaturated-black;
			margin-left: 12px;
		}
		.attachment-uploader-dropdown{
			margin-left: 50%;
			.attachment-title{
				margin: 0;
				transform: translate(-50%);
			}
			.attachment-arrow{
				display: none;
			}
			.ember-basic-dropdown-content{
				margin-top: 83px;
			}
		}
		.attach-files {
			margin: 16px;
			padding: 16px;
			.attachment-uploader {
				width: 208px;
				margin: 0 auto;
			}
		}
	}
	.attach-files{
		background: none;
		margin: 0;
		padding: 0;
		.attachment-uploader {
			.attachment-limit{
				display: none;
			}
			.attachment-title{
				font-weight: $text-weight-semi-bold;
			}
		}
		.attach-help-size {
			display: none;
		}
		input[type="file"] {
			& + label {
				border: none;
			}
			&:focus + label span.attachment-title {
				border-color: $linkColor;
			}
		}
	}
	.attachment-list {
		@include flexbox;
		@include flex-direction(column);
		.list-file-item {
			width: 450px;
			height: 56px;
			.file.local {
				max-width: 100%;
			}
			.file-info {
				@include flexbox;
				@include flex-direction(column);
				width: 300px;
			}
			.file-icon {
				width: 24px;
				height: 24px;
				margin-left: 8px;
			}
		}
	}
}

