.custom-popup {
	position: absolute;
	width: 512px;
	background: $app-white;
	box-shadow: 0px 2px 18px rgba(18, 52, 77, 0.2);
	border-radius: 4px;
	z-index: 999;
	.action-btns {
		height: 56px;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		padding: 0 8px;
		background-color: $background-color-2;
		border-bottom-left-radius: 6px;
		border-bottom-right-radius: 6px;
	}
}
