.purchase-order {
	@import "po_attachment";
	.ember-power-select-trigger-multiple-input{
		min-width:50px;
		padding:0;
	}
	.break {
		flex-basis: 100%;
	}

	.svg-16 { width: 16px; height: 16px; }

	.po-details-btns {
		margin-left: auto;

		.more-btn {
			padding: 7px;
		}
		.receive-items-btn{
			display: inline-block;
		}
	}
	.received-status {
		svg{
			width: 18px;
			height: 18px;
			float: right;
			margin-top: 2px;
		}
	}
	.grey-cell-bg{
		background-color: $background-color-2;
	}
	.content-wrap .po-details-tabs{
		border-bottom: 0;
	}

	.dot-loading {
		padding: 18% 0;
		text-align: center;
	}

	.page-content {
		background-color: $app-white;
	}
	.content-wrap {
		padding: 15px 0px;

		.flex-wrap {
			display: flex;
			flex-wrap: wrap;
			width: 960px;
			margin-bottom: 5px;

			.flex-item { width: 40%; }

			.paragraph-field{ width: 100%; }
		}

		.field-label {
			color: $disabled-text-color;
			font-size: $text-normal;
			font-weight: $text-weight-semi-bold;
		}

		.multi-line-items {
			white-space: pre-line;
			line-height: 22px;
			word-break: break-all;
			padding-right: 20px;
		}

		.field-spacing {
			margin-top: 16px;
		}
		.margin-bottom-12 {
			margin-bottom: 12px;
		}
		.lt-head{
			th{
				text-transform: none!important;
			}
		}
		.lt-body{
			td{
				border-right: 1px solid $primary-border!important;
				&:last-child{
					border-right: hidden!important;
				}
			}

		}
		.po-tally-wrap {
			margin: 12px 0;
			text-align: right;

			.po-tally-items {
				text-align: left;
				display: inline-block;
			}
		}
		.po-tally-label {
			padding: 10px 0;
			display: inline-block;
			width: 140px;
		}
		.po-tally-value {
			display: inline-block;
			width: 120px;
		}

		.tabs__items {
			border-bottom: 2px solid $background-color-2;
		}

		.bold-title {
			font-weight: $text-weight-semi-bold;
			color: $primary-text-color-1;
			font-size: $title-small;
			&.po-total-amt{
				font-size: $title-normal;
			}
		}

		.purchase-details-wrap {
			margin-top: 32px;
			.pd-wrap{
				margin-top: 14px;
				.pd-items{
					margin-bottom: 4px;
				}
			}
		}
		.po-items-table {
			th, td {
				box-sizing: content-box;
			}
		}
	}
	.po-options-dd {
		display: inline-block;
		ul {
			min-width: 50px;
		}
		.options-item {
			min-width: 82px;
			svg {
				width: 12px;
				height: 12px;
				float: right;
				margin: 2px 0px;
			}
		}
		.ember-basic-dropdown-content {
			min-width: auto;
		}
	}
	.po-title-wrap {
		margin: 0px 16px 16px;
		position: relative;

		.po-title {
			margin-bottom: 6px;
			font-size: $text-large;
			width: calc(100% - 164px);
			word-break: break-word;
			color: $primary-text-color-1;
		}

		.created-by {
			color: $inputHoverBorder;
			font-size: $title-small;
		}

		.po-status-dd {
			float: right;
			display: flex;
			margin-top: 16px;
			height: 32px;
			.po-status-label{
				margin-top: 6px;
				margin-right: 8px;
			}
			.readonly-status{
				margin-right: 16px;
				margin-top: 6px;
				font-weight: $text-weight-semi-bold;
			}
		}
		.ember-basic-dropdown-content {
			min-width: 150px;
			right: -2px;
		}
		.po-status-dd-trigger {
			display: flex;
			align-items: center;
			margin-right: 0;
			padding-right: 4px;

			svg {
				width: 16px;
				height: 16px;
				margin: 0 4px;
			}
		}
	}
	.no-approvals-wrap{
		text-align: center;
		margin-left: 60%;

		.disabled-btn{
			opacity: 0.3;
			filter: grayscale(1);
			cursor: default;
		}

		.no-approvals-desc{
			font-weight: 600;
			padding-bottom: 26px;
			color: $app-form-label-color;
		}

		.no-approvals-found{
			background: url(/a/assets/images/app-common/thumbs-up.svg) 15% 75% no-repeat;
			padding-top: 0;
			background-position: center;
			height: 200px;
			display: block;
			margin: 100px auto;
			margin-bottom: 25px;
		}

		label {
			padding-bottom: 26px;
			color: $app-form-label-color;
			font-weight: $text-weight-semi-bold;
		}

		button {
			margin: 0 auto;
		}
	}
	.approval-indicator-wrap{
		display: flex;
		padding: 5px 7px;
		border-radius: 4px;
		margin-left: 10px;
		height: 24px;
		align-items: center;
		font-size: 12px;
		box-sizing: border-box;
		.indicator-icon{
			padding-right: 7px;
			width: 12px;
			height: 12px;
		}
		&.rejected-indicator{
			color: $btnDangerBorder;
			background: $alertWarningBackground;
			border: 1px solid $alertWarningBorder;
		}
		&.approved-indicator{
			color: $alertSuccessColor;
			background: $alertSuccessBackground;
			border: 1px solid $alertSuccessBorder;

		}
		&.cancelled-indicator{
			color: $alertCancelColor;
			background: $btnDisabledBackground;
		}

		&.requested-indicator{
			display:none;
		}
	}
	.requestApprovalsWrap {
		margin-bottom: 25px;
	}

	.purhcase-order-approvals{
		width:60%;
		position: relative;
		padding-bottom: 100px;

		.request-approval-btn {
			margin-bottom: 25px;
		}
		.link-btn {
			display: flex;
			align-items: center;
			background: none;
			border: none;
			padding: 0;
			color: $app-primary;
			outline: none;
			svg {
				margin-right: 6px;
			}
		}

		svg.approve{
			fill: $app-green;
		}
		svg.reject{
			fill: $btnDangerActiveBackground;
		}
		svg.already-approve-reject {
			fill: $app-muted-color;
		}

		.request-approvals{
			display: flex;
			align-items: center;
			svg{
				margin-right: 5px;
				width: 16px;
			}
			.add-approval-link{
				margin-bottom: 2px;
			}
		}

		.article-card{
			padding: 0px;
			box-shadow: none;
			label:before{
				content: "";
				padding-top: 32px;
				display: inline-block;
			}
			span.ember-pill{
				font-weight: 500;
			}
		}

		.po-approvals-dd {
			padding-left: 20px;
			cursor: pointer;
			.disabled {
				svg.chevron-down {
					fill: $disabled-text-color;
				}
			}
		}
		.readonly-approval-type{
			margin-left: calc(100% - 173px);
			color: $secondary-text-color-1;
			font-style: italic;
		}
		.all-approval-type{
			padding-left: 13px;
		}
		.majority-approval-type{
			padding-left: 20px;
		}

		.po-approvals-dd-trigger {
			display: flex;
			align-items: center;
			font-size: $text-normal;
			font-weight: $text-weight-semi-bold;

			svg {
				width: 16px;
				height: 16px;
				margin: 0 4px;
			}
		}
		.approval-type-text {
			font-size: $text-normal;
			font-weight: $text-weight-normal;
			width: 174px;

			&[active='true']{
				font-weight: $text-weight-semi-bold;
			}
		}
		.search-users{
			form{
				margin-bottom:25px;
			}
			.ember-basic-dropdown{
				input{
					padding-left: 10px;
				}
			}
		}
		.po-approval-btns-wrap{
			text-align: right;
			.btn{
				margin-right: 0px;
				margin-left: 8px;
			}
		}

		.po-approvals-create-wrap{
			border: 1px solid $primary-border;
			padding: 20px;
			border-radius: 5px;
			margin: 6px 0;
			.ember-form {
				display: flex;
				align-items: center;
				.form-fields.search-users {
					flex: 1;
				}
			}
			.ember-power-select-trigger{
				padding-top: 2px;
				padding-bottom: 2px;
			}
		}

		.approval-cards-wrap{
			margin-bottom: 25px;
		}

		.approval-level {
			margin-bottom: 22px;
		}

		.approval-type-note {
			display: flex;
			align-items: center;
			padding-bottom: 10px;
			font-weight: $text-weight-medium;
			color: $secondary-text-color-2;
			svg {
				fill: $secondary-text-color-2;
				&.requested {
					margin-right: 8px;
					margin-left: 2px;
				}
				&.resolved {
					height: 18px;
					width: 18px;
					margin-right: 6px;
				}
				&.cancelled {
					width: 28px;
					margin-left: -5px;
					background: none;
				}
			}
		}

		.approval-cards {
			border-radius: 4px;
			border: 1px solid $primary-border;
			hr {
				margin: 0 20px;
				height: 0;
				border-top: 0;
				border-bottom: 1px solid $primary-border;
			}
		}

		.approval-card{
			box-shadow: none;
			display: flex;
			flex-wrap: wrap;
			border-radius: 0;
			padding: 12px 20px;
			background-color: inherit;
			&:hover{
				background: $background-color;
				.approval-actions{
					visibility: visible;
				}
			}
			.avatar__initial{
				cursor: default;
				font-size: 16px;
				width: 24px;
				height: 24px;
			}
			.approval-person{
				width: 30%;
				display: flex;
				align-items: center;
				.approver-name{
					padding: 8px;
					margin-left: 4px;
					button{
						text-align: left;
					}
				}
			}
			.approval-remark{
				padding-left: 20px;
				margin-top: 8px;
				font-weight: lighter;
			}
			.approval-message{
				flex: 1;
				display: flex;
				flex-direction: column;
				justify-content: center;
				span{
					display: flex;
					align-items: center;
					font-weight: $text-weight-light;
					svg{
						margin-right: 6px;
						width: 16px;
						height: 16px;
					}

				}
			}
			.approval-actions{
				width: 30%;
				display: flex;
				flex-direction: row-reverse;
				visibility: hidden;
				&.active {
					visibility: visible;
				}
				.requester, .approver {
					display: flex;
					span{
						display: flex;
						cursor: pointer;
						margin-left: 18px;
						align-items: center;
						svg{
							margin-right: 8px;
							width: 16px;
							height: 16px;
						}
					}
					.reminder-sent{
						cursor: default;
						svg{
							fill: $btnSuccessBackground;
						}
					}
				}
				.approver {
					position: relative;
					.approve-reject-popup {
						.ember-form {
							padding: 28px 32px;
							.form-fields {
								label {
									display: inline-block;
								}
								span {
									display: inline;
									margin-left: 0;
								}
							}
						}
					}
				}
			}
		}
	}

	//Style for purhcase order associations tab
	.purhcase-order-associations{
		.no-association-wrap {
			text-align: center;
			.no-associations-found {
				height: 200px;
				margin: 100px auto 30px;
			}
			.no-associations {
				font-weight: 600;
				padding-bottom: 26px;
				color: $app-form-label-color;
			}
		}
		.associate-request{
			display: inline-block;
			margin-bottom: 18px;
			button {
				display: flex;
				align-items: center;
				background: none;
				border: none;
				padding-left: 0;
				color: $app-primary;
				outline: none;
			}
			svg{
				width: 16px;
				height: 16px;
				margin-right: 6px;
				fill: $app-icon-color;
			}
		}
	}
	.associated-sr-card {
		width: 536px;
		padding: 10px 12px;
		border-radius: 3px;
		margin-top: 10px;
		border: 1px solid $app-border-primary;
		box-shadow: inset 0 1px 2px 0 rgba(24,50,71,0.05);
		&.asset-received-card{
			width: 720px;
		}
		.asset-name{
			color: #12344D;
		}

		.associated-sr-ticket {
			display: flex;
			.btn-delete-association {
				visibility: hidden;
				background: none;
				border: none;
				outline: none;
				padding-top: 4px;
				padding-left: 0;
			}
			.service-request-ticket {
				overflow: hidden;
				&.hover-transition {
					margin-left: -20px;
					transition-property: margin-left;
					transition-duration: 0.1s;
				}
				.subject {
					display: flex;
					padding: 0;
					line-height: 24px;
					color: $primary-text-color-1;
					span:first-child {
						white-space: nowrap;
						padding-right: 5px;
						color: $secondary-text-color-1;
					}
					span:last-child {
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}
				}
				.additional-info {
					font-size: $text-normal;
					color: $primary-text-color-1;
					> span {
						position: relative;
						padding-right: 20px;
						&:not(:last-child)::after {
							content: '';
							position: absolute;
							top: calc(50% - 2px);
							right: 10px;
							border: 2px solid $disabled-text-color;
							border-radius: 50%;
						}
						span:first-child {
							color: $disabled-text-color;
						}
					}
				}
			}
		}
		&:hover {
			.btn-delete-association {
				visibility: visible !important;
			}
			.associated-sr-ticket {
				.asset-title{
					color: $app-primary;
				}
				.hover-transition {
					margin-left: 0;
				}
				.subject.link {
					color: $app-primary;
					cursor: pointer;
					span:first-child {
						color: $app-primary;
					}
				}
			}
		}
	}
	.assets-received{
		.product-type-dd{
			cursor: pointer;

			.basic-dropdown-item {
				padding-right: 30px;
			}

		}
		.assets-received-dd-text{
			margin: 6px 0px 9px;
			color: $secondary-text-color-2;
		}
		.ember-badge{
			color:$app-black;
		}

		.no-association-wrap {
			text-align: center;
			.no-associations-found {
				height: 200px;
				margin: 100px auto 30px;
			}
			.no-associations {
				font-weight: 600;
				padding-bottom: 26px;
				color: $app-form-label-color;
			}
		}
	}
}

//Style for purhcase order associations tab modal
.associate-service-request-modal {
	.search-input-wrapper {
		margin-top: 20px;
	}
	.ember-modal-body {
		height: calc(100% - 185px) !important;
	}
	.ember-alert-warning {
		margin-top: 16px;
		background-color: $alertWarningBackground;
		border-color: $alertWarningBorder;
		p::before {
			border-left-color: $alertWarningBorder;
		}
	}
	.service-request-card {
		margin-bottom: 8px;
		padding: 0;
		border: 1px solid $app-border-primary;
		box-shadow: inset 0 1px 2px 0 rgba(24,50,71,0.05);
		.service-request-ticket {
			display: flex;
			padding: 12px;
			cursor: pointer;
			.ticket-info {
				margin-left: 5px;
				overflow: hidden;
				.subject {
					color: $primary-text-color-1;
					font-weight: $text-weight-semi-bold;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
					padding-bottom: 2px;
					span:first-child {
						color: $secondary-text-color-2;
						padding-right: 2px;
					}
				}
				.status {
					font-size: $text-normal;
					color: $secondary-text-color-2;
				}
			}
		}
	}
	.empty-results-wrap {
		height: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		text-align: center;
		img{
			height: 200px;
			width: 200px;
			margin-bottom: 30px;
		}
	}
}
.approve-po-modal{
	.ember-modal-header{
		display: none;
	}
	.ember-modal-body {
		margin-top: 28px;
		.ember-form .textarea-field.form-field{
			margin-bottom: 0px;
		}
	}
}

.sr-dissociate-confirm-modal {
	.warning-info {
		padding-bottom: 5px;
	}
}
@import "add_to_inventory";
