.ember-modal-dialog.ati-list-modal {
	.ember-modal-header{
		padding-bottom: 0px;
	}

	.ember-modal-body {
		height: calc(100% - 275px);
	}
}

.ati-list-modal {
	.buffer-el{
		height:100px;
	}
	.ati-detail-box {
		padding-top: 18px;

		.ember-form form .form-field > label {
			height: 28px;
		}
	}
	.product-ulist{
		margin-top:20px;
	}

	.no-products{
		text-align: center;
		margin-top: 150px;

		img{
			margin-bottom: 15px;
		}
	}

	#add-to-inventory-header{
		h3 {
			font-weight: 600;
			font-size: 18px;
			display: inline-block;
		}

		.info-btn{
			fill: #888;
			transform: translate(0px,4px);
			height: 20px;
			width: 20px;
		}
	}

	$grey:  #727681;

	.grey {
		color: $grey;
	}

	ul {
		margin-top: 20px;
	}

	.sub-heading{
		margin-top:10px;
	}

	.ati-back-to-list{
		height: 20px;
		box-sizing: content-box;

		.icon {
			width: 20px;
			height: 20px;
			left: 24px;
			fill: #2C5CC5;
			position: absolute;
		}
	}

	.item-list--card{
		cursor:pointer;
		padding: 14px 12px;
		margin: 8px auto;
		display: flex;
		justify-content: space-between;
		align-items: center;
		border: 1px solid #CFD7DF;
		border-radius: 3px;

		.hover-content{
			display:none;
		}
		&.in-progress{
			opacity: 0.3;
			cursor: auto;



			&:hover{
				background: white;
				.left-sided,.right-sided{
					display:none;
				}

				.hover-content{
					display: block;
				}

			}
		}

		.sub-details{
			color: $secondary-text-color-1;
			font-size: 12px;
		}

		.left-sided{
			display: flex;
			align-items: center;
		}

		.large-dot {
			background: $grey;
			border-radius: 50%;
			width: 3px;
			height: 3px;
			margin: 8px;
		}
		&:hover{
			background: #E7EBEF;
		}
	}

	.asset-table{
		input{
			border: 1px solid #CFD7DF;
			padding: 8px;
		}

		td {
			padding: 8px 16px 8px 0;
		}

		.inline-wrapper .error-message {
			width: auto;
			font-size: 12px;
			border: none;
			background-color: transparent;
			font-weight: 400;
			box-shadow: none;
			padding: 0;
			position: relative;
			top:0;
		}
	}

	.product-box {
		padding: 10px 0px;



		h3 {
			font-size: 18px;
			font-weight: 600;
		}

		.sub-details{
			color: $secondary-text-color-1;
			margin: 8px auto;
		}

		.item-status{
			margin-top:8px;
		}

		.large-dot{
			background: $grey;
			border-radius: 50%;
			width: 4px;
			height: 4px;
			margin: 6px;
			display: inline-block;
			vertical-align: middle;
		}
	}

	.full-width-line{
		width: calc(100% + 64px);
		margin: 0px;
		margin-left: -32px;
	}

	.top-input-box {

		.main-box {
			display: flex;
			flex-direction: row;
			margin: 0 -32px;
			border-top: 1px solid #CFD7DF;
			border-bottom: 1px solid #CFD7DF;
			padding: 16px 32px;

			> div {
				flex:1;
				margin-right: 16px;

				input {
					width: 60px;
					margin: 0 6px;
					border-color: #CFD7DF;
				}
			}

			.inline-wrapper {
				display: flex;
				align-items: center;

				.ember-text-field {
					padding: 8px 10px;
				}

				.ember-tooltip-target {
					margin-left: 5px;
				}

				.error-message {
					left: 40px;
					top: 35px;
					font-size: 12px;
					line-height: 1.2;
					width: auto;
					border-radius: 2px;
				}
			}
		}

	}

	.add-or-associate-tab-switch{
		display: flex;
		align-items: center;
		margin-bottom: 8px;
		> div{
			padding:8px 16px;
			margin: 8px 16px 8px 0px;
			cursor: pointer;
			&.active{
				background: #E5F2FD;
				border: 1px solid #BEDDFE;
				border-radius: 20px;

			}
		}
	}

	.border-right{
		border-right: 1px solid #CFD7DF;
	}

	//For update existing tab for consumables
	.update-existing {


		.no-assets-ue{
			display: flex;
			flex-direction: column;
			align-items: center;
			height: 300px;
			justify-content: space-around;

			img{
				transform: translate(-10px);
				padding: 30px;
			}
		}
		&--list{
			.card {
				display: flex;
				justify-content: space-between;
				cursor: pointer;
				border: 1px solid #EBEFF3;
				padding: 16px;
				margin: 8px auto;
				box-shadow: none;

				&:hover,&.active {
					border: 1px solid #2B5BC4;

					.checkmark {
						background: transparent;
					}
				}

				.left-container {
					display:flex;
					align-items: center;
				}

				.checkmark {
					width: 20px;
					height: 20px;
					background: #EBEFF3;
					border-radius: 50%;
					fill: #EBEFF3;
					margin-right: 16px;

					&.active {
						fill: white;
						background: #2B5BC4;
					}
				}

				.status {
					color: #475867;
				}
			}
		}
	}
}

.warranty-field {
	display: flex;

	.field-choice {
		width: 100px;
	}

	span {
		margin: auto 10px;
	}

	.ember-basic-dropdown
	.ember-power-select-search
	.search-container {
		left: 8px;
	}
}
